.container5 {
    position: relative;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #f0f0f0;
    /* 新增背景色以提升對比度 */
}

.guidePrompt5 {
    display: none;
    /* 預設隱藏 */
    position: absolute;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
    background-color: rgba(255, 255, 255, 0.8);
    padding: 10px;
    border-radius: 5px;
    font-size: 18px;
    text-align: center;
    z-index: 10;
    /* 確保提示在頂層 */
}

.endGameButton5 {
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    padding: 10px 20px;
    font-size: 16px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    z-index: 10;
    /* 確保按鈕在頂層 */
}

.endGameButton5:hover {
    background-color: #45a049;
}

/* 添加媒體查詢以提高響應式設計 */
@media (max-width: 768px) {
    .guidePrompt5 {
        font-size: 16px;
        padding: 8px;
    }

    .endGameButton5 {
        padding: 8px 16px;
        font-size: 14px;
    }
}

@media (max-width: 480px) {
    .guidePrompt5 {
        font-size: 14px;
        padding: 6px;
    }

    .endGameButton5 {
        padding: 6px 12px;
        font-size: 12px;
    }
}